.app-wrapper {
  margin: 0 auto;
  width: 1200px;
  min-height: 100vh;
  display: grid;

  grid-template-areas:
      "h h"
      "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
}

.app-wrapper-content {
  grid-area: c;
  background-color: cornflowerblue;
  position: relative;
}
